import Msg from './InnerMsg'
import  Router  from 'next/router';
import Link from 'next/link'

const linkStyle ={
    marginRight: 100
};
class navi extends React.Component{
    constructor(props){
        super(props);
        this.state= {
            uId :''
        }
       
    }
    componentDidMount(){
        const uId =  localStorage.getItem('uId')
        this.setState({
            uId: uId
        })
    }
    handleClick(e){
        e.preventDefault();
        localStorage.removeItem('role')
        localStorage.removeItem('uId')
        Router.push({
            pathname: '/signin'
        })
    }
    render(){
        return(
            <div>
            {this.props.role==null&&
                <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
                
                
                <div className="collapse navbar-collapse" id="navbarColor01">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item active">
                            <a style={linkStyle} title='Home page' className="nav-link" href="/">Home <span className="sr-only">(current)</span></a>
                        </li>
                        <li className="nav-item">
                            <a style={linkStyle} className="nav-link" href="/signin">Sign in</a>
                        </li>
                        <li className="nav-item">
                            <a style={linkStyle} className="nav-link" href="/Signup">Sign up</a>
                        </li>
                        <button type="button" className="btn btn-primary btn-lg" onClick={this.handleClick.bind(this)}>Sign out</button>                          
                    </ul>
                   
                </div>
                </nav>
            }
            {this.props.role=='Contributor'&&
                <nav className="navbar navbar-expand-lg navbar-dark bg-primary">                 
                <div className="collapse navbar-collapse" id="navbarColor01">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item active">
                            <a style={linkStyle} title='Home page' className="nav-link" href="/">Home <span className="sr-only">(current)</span></a>
                        </li>
                        <li className="nav-item">
                            <a style={linkStyle} className="nav-link" href="/Contribute">Contribute</a>
                        </li>
                        <li className="nav-item">
                            <Link href="/myEntity/[id]" as={`/myEntity/${this.state.uId}`} >           
                                <a style={linkStyle} className="nav-link" >My Entity</a>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <a style={linkStyle} className="nav-link" href="/Profile">Profile</a>
                        </li>
                    </ul>
                    <div> 
                         
                        <button type="button" className="btn btn-primary btn-lg" onClick={this.handleClick.bind(this)}>Sign out</button>                          
                    </div>
                </div>       
                </nav>
            }

            {this.props.role=='Curator'&&
            <nav className="navbar navbar-expand-lg navbar-dark bg-primary">                 
            <div className="collapse navbar-collapse" id="navbarColor01">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">
                        <a style={linkStyle} className="nav-link" href="/">Home <span className="sr-only">(current)</span></a>
                    </li>
                    <li className="nav-item">
                        <a style={linkStyle} className="nav-link" href="/Contribute">Contribute</a>
                    </li>
                    <li className="nav-item">
                        <Link href="/reEntity/[id]" as={`/reEntity/${this.state.uId}`} >           
                            <a style={linkStyle} className="nav-link" >Verify Entity</a>
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link href="/myEntity/[id]" as={`/myEntity/${this.state.uId}`} >           
                            <a style={linkStyle} className="nav-link" >My Entity</a>
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a style={linkStyle} className="nav-link" href="/Profile">Profile</a>
                    </li>
                </ul>
                <div> 
                   <Msg />
                   <button type="button" className="btn btn-primary btn-lg" onClick={this.handleClick.bind(this)}>Sign out</button>
                </div>
            </div>       
            </nav>
        }

            {this.props.role=='Manager'&&
            <nav className="navbar navbar-expand-lg navbar-dark bg-primary">                 
            <div className="collapse navbar-collapse" id="navbarColor01">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">
                        <a style={linkStyle} className="nav-link" href="/">Home <span className="sr-only">(current)</span></a>
                    </li>
                    <li className="nav-item">
                        <a style={linkStyle} className="nav-link" href="/Contribute">Contribute</a>
                    </li>
                   
                    <li className="nav-item">
                        <Link href="/reEntity/[id]" as={`/reEntity/${this.state.uId}`} >           
                            <a style={linkStyle} className="nav-link" >Release Entity</a>
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link href="/account" as ="/account">
                            <a style={linkStyle} className="nav-link">account</a>
                        </Link>
                       
                    </li>
                    <li className="nav-item">
                        <Link href="/myEntity/[id]" as={`/myEntity/${this.state.uId}`} >           
                            <a style={linkStyle} className="nav-link" >My Entity</a>
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a style={linkStyle} className="nav-link" href="/Profile">Profile</a>
                    </li>
                
                </ul>
                <div> 
               
                    <button type="button" className="btn btn-primary btn-lg" onClick={this.handleClick.bind(this)}>Sign out</button>
                </div>
            </div>       
            </nav>
        }
            </div>
        );
      
    }
    
}

export default navi;